import { Article } from "../Article";
const content = `Backfooder est une plateforme de partage de bonnes adresses (restaurants et bars)  ses amis et sa famille. Louverture au public sest faite en avril 2022.

Dans le cadre dun projet personnel, jai dcid de crer cette plateforme pour amliorer et dcouvrir des comptences.

Pour cette ralisation jai ralis un planning, des tests d'ergonomie, un backend et un frontend.

Les enjeux de ce projet taient principalement personnels. Cest un dfi personnel que je me suis impos. Le projet doit tre mis en production et une campagne de marketing doit tre lance. Le but est datteindre la centaine dutilisateurs dans les deux premiers mois.  

Un dfi suivant la mise en production est la maintenance et lvolution de la plateforme tout en acceptant de plus en plus dutilisateurs.  

La mise en uvre de cette plateforme sest faite en plusieurs tapes :
* Dveloppement du concept
    * Systme de notation des restaurants
    * Systme d'interactions entre les utilisateurs
    * Ergonomie et visualisation des adresses (restaurants et bars)
* Dfinition de la charte graphique
* Cration dun modle de donnes
* Choix technologiques au niveau du dveloppement
    * Backend: C# .Net Core
    * Frontend: React, Typescript, avec TailwindCss
    * Base de donnes: MongoDB
*   Choix technique concernant lintgration de la golocalisation de lutilisateur.
*   Dveloppement dun MVP (produit viable minimum) qui pourra accueillir les premiers utilisateurs :
    *   Un frontend respectant les points cits ci-dessus
    *   Un backend et une base de donne
*   Choix et dploiement du projet sur des solutions dhbergement viable

Plusieurs difficults ont t rencontres. Lune dentre elle tait dans le choix technique concernant le backend. Une autre tait la charte graphique : il faut une application viable que les utilisateurs aiment utiliser.


Jai pu amliorer mes comptences et entrer dans le monde de la mise en production de projets et en marketing.
`
export function BackfooderArticle() {
    return (
        <>
            <h1 className="m-auto text-center">Backfooder</h1>
            <Article md={content} />
        </>
    )
}